body{
	background-color: rgb(177, 210, 223);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.todo-item{
  width: 300px;
  min-height: 100px;
  border-bottom: 1px solid #dddddd;
  background-color: white;
  margin-bottom: 10px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input{
  width: 250px;
  height: 50px;
  border: none;
  outline: none;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 10px 0 0 10px;
  box-sizing: border-box;
}

.add-btn{
  width: 50px;
  padding: none;
  background-color: #dddddd;
  border: none;
  height: 50px;
  border-radius: 0 10px 10px 0;
}